const install = ( Vue, vm ) => {
	Vue.prototype.$u.http.setConfig( {
		baseUrl,
		// trueresponseresponse.data
		// truethis.$u.http.interceptor.response
		// originalData: true,
		// content-type
		// header: {
		// 	'content-type': 'xxx'
		// }
	} )
	// Token
	Vue.prototype.$u.http.interceptor.request = ( config ) => {
		config.header.Token = 'xxxxxx'
		const accessToken = vm.vuex_access_token
		const expiresIn = vm.vuex_expires_in
		if ( accessToken ) {
			config.header = { 'Authorization': `Bearer ${accessToken}` }
		}
		if( whileList.indexOf(config.url)===-1 && !accessToken) {
			//
			uni.navigateTo({
				url: '/pages/auth/login?navigateType=1'
			})
			return false;
		}
		// vuextokenuViewvuexhttps://uviewui.com/components/globalVariable.html
		// config.header.token = vm.token;
		
		// uViewvuex$store.state
		// config.header.token = vm.$store.state.token;
		
		// tokenglobalDatagetApp().globalData
		// config.header.token = getApp().globalData.username;
		
		// tokenStorageStorage
		// const token = uni.getStorageSync('token');
		// config.header.token = token;
		
		return config
	}
	// 
	Vue.prototype.$u.http.interceptor.response = ( res ) => {
		const code = res.code || 200
		// originalDatatrue
		// res.statueCoderes.data.code
		const msg = errorCode[code] || res.msg || errorCode['default']
		if ( code == 200 ) {
			// originalDatatruereturnthis.$u.postthen
			return res.data
		} else {
			if (code === 401) {

				uni.navigateTo({
					url: '/pages/auth/login?navigateType=2'
				})
			} else if (code === 500) {
				uni.showToast({
					title: msg,
					icon: 'none',
					duration: 2000,
					position: 'top',
				});
			}else if (code === 403) {
				console.log(4030303030303)
				uni.navigateTo({
					url: '/pages/auth/login?navigateType=3'
				})
			}
			else if (code !== 200) {
				uni.showToast({
					title: msg,
					icon: 'none',
					duration: 2000,
					position: 'top',
				});
			}
			return false
		}
	}
}
export default {
	install
}
